import React from "react";

import './User.scss';
import avatarPlaceholder from '../../../../Shared/images/avatarPlaceholder.png';
import detect from "detect.js";
import Blazy from 'blazy/blazy';
import Tooltip from "@material-ui/core/Tooltip";

export default class User extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            userImg: this.props.user.photo,
            emailIsLong: false
        };

        const userAgent = detect.parse(navigator.userAgent);
        this.isWindows = /Windows/i.test(userAgent.os.family);
        this.isFirefox = /Firefox/i.test(userAgent.browser.family);

        this.user = this.props.user;
    }

    componentDidMount() {
        const emailElem = document.querySelector(`.user.user${this.props.idx} .user__info-email`);
        if (emailElem.scrollWidth > 209) this.setState({emailIsLong: true});
    }

    render() {
        let bLazy = new Blazy();


        return (
            <div className={`user user${this.props.idx}`}>
                <img className="user__photo b-lazy" src={avatarPlaceholder} data-src={this.user.photo} alt="user"/>
                <div className="user__info">
                    <div
                        className={`user__info-name${this.isWindows || this.isFirefox ? ' overpassFixUserName' : ''}`}
                        title={this.user.name}
                    >{this.user.name}</div>
                    <div className="user__info-position">{this.user.position}</div>
                    <Tooltip
                        title={this.user.email}
                        placement="bottom"
                        disableFocusListener={!this.state.emailIsLong}
                        disableHoverListener={!this.state.emailIsLong}
                        disableTouchListener={!this.state.emailIsLong}
                    >
                        <p className="user__info-email">{this.user.email}</p>
                    </Tooltip>
                    <div className="user__info-phone">{this.user.phone}</div>
                </div>

            </div>
        )
    }
}